body {
    margin: 0;
    padding: 0;
    font-family:
        -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
        "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
        "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family:
        source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

body {
    background-color: #222222;
    color: #66CCFF;
}

a {
    color: #66CCFF;
}

.App {
    width: 30%;
    margin: auto;
}

.frostmark-bookmark-list {
    background-color: #333333;
}

/* TreeView vertical main line */
.tree-view {
    border-left: 1px solid #66CCFF55;
}

/* TreeView child horizontal side line */
.tree-view_children > div.info:before {
    content: "\23AF\23AF";
    font-family: monospace, monospace;
    font-size: 15px;
    color: #66CCFF55;
}
.tree-view_children > div.info {
    -webkit-transform: translate(-17px);
            transform: translate(-17px);
}

.bookmarkLabel:hover {
    color: #FFF;
    cursor: pointer;
}

.bookmarkLabelForm {
    display: flex-box;
}

.bookmarkLabelParagraph {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

.bookmarkLabelInput {
    width: 50vw;

    /* make <input> and <select> the same size */
    -ms-box-sizing: border-box;
    box-sizing: border-box;
}

